<template>
  <v-ons-page>
    <div class="content">
      <v-ons-pull-hook :action="loadItem" @changestate="state = $event.state">
        <span v-show="state === 'initial'"> Pull to refresh</span>
        <span v-show="state === 'preaction'"> Release </span>
        <span v-show="state === 'action'"> Loading </span>
      </v-ons-pull-hook>

      <v-ons-list modifier="inset">
        <v-ons-list-header>Online</v-ons-list-header>
        <v-ons-list-item
          tappable
          v-for="(user, key) in onlineUsers"
          :key="key"
          @click="selectUser(user)"
        >
          <div class="left">
            <img
              class="list-item__thumbnail"
              src="http://localhost:8081/assets/face.jpg"
            />
          </div>
          <div class="center">
            <span class="list-item__title">
              {{ user.user_id === currentUser.id ? "You" : user.name }} </span
            ><span class="list-item__subtitle">
              Joined: {{ user.created_on | mDate }}
            </span>
          </div>
        </v-ons-list-item>
      </v-ons-list>
    </div>
  </v-ons-page>
</template>
<script>
export default {
  data() {
    return {
      state: "initial",
      currentUser: this.$store.getters.user,
      onlineUsers: [],
      userLeft: Object,
      selectedUser: null
    };
  },
  sockets: {
    users_online(users) {
      this.onlineUsers = users;
    },
    user_left(user) {
      this.userLeft = user;
    }
  },

  methods: {
    loadItem(done) {
      setTimeout(() => {      
        this.items = [this.items, this.items.length + 1];
        done(); // 08137372222
      }, 400);
    },
    selectUser(id) {
      this.selectedUser = id;
    }
  }
};
</script>
