import Vue from 'vue'
import Vuex from 'vuex'
import Login from './../components/Login.vue'
// import Home from './../components/Home.vue'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pageStack: [Login],
    user: null,
    online_users: []
  },
  mutations: {
    setPage(state, pageLoad) { 
      state.pageStack.pop();
      state.pageStack.push(pageLoad);
    },
    setUser(state, userData) {
      state.user = userData;
    },
  },
  actions: {
    setPage(state, pageLoad) {
      state.commit('setPage', pageLoad);
    },
    setUser(state, userData) {
      state.commit('setUser', userData);
    },
  },
  getters: {
    page(state) {
      return state.pageStack
    },
    user(state) {
      return state.user
    },
  }
})